const BASE_URL = 'http://localhost:880'


//
export async function post(url, data = {}, other = {}) {
	let finnally_url = BASE_URL + url;
    if (other.tip) { //catchtip  err  cancel 		
		let res = await uni.showModal({
			title:"",
			content:other.tip,
			confirmText:"",
			cancelText:""
		})
		if(res[1].confirm){
			return api('POST', finnally_url, data, other)
		}else{
			return Promise.reject("cancel")
		}
		
        

    } else {
        return api('POST', finnally_url, data, other)
    }
}

export async function get(url, data = {}, other = {}) {
	let finnally_url = BASE_URL + url;
    if (other.tip) { //catchtip  err  cancel 		
		await uni.showModal({
			title:"",
			content:other.tip,
			confirmText:"",
			cancelText:""
		})
		
        return api('GET', finnally_url, data, other)

    } else {
        return api('GET', finnally_url, data, other)
    }
}




// 
const api = (method,url,data,other={})=>{
	let {
	    load={text:"..."},// ,  ,text  ""
	    code = {}
	} = other
	let {successCode = 200, needSuccessCode = true } = code
	
	// 
	toggleLoading(load, true)
	
	
	// 
	let header = {
		'Content-Type' : "application/json; charset=utf-8",
		"Authorization" : "Bearer " + uni.getStorageSync('token')
	}
	header = Object.assign(header,data.header)
	
	return new Promise((resolve,reject)=>{
		uni.request({
			url:url,                 // url
			method: method,         // post
			data: data || {},                 //  
			header,                                   //
			// timeout
			success: (res)=>{
				if (needSuccessCode &&  successCode != res.data.code) {//code
					uni.showToast({
						title:res.data.info,
						duration: 3 * 1000
					})
				    reject(res.data)//
				}else{
					resolve(res.data)// res.data.data  res.data.!data
				}
			},
			fail: (err)=>{
				reject(err)
			},
			// 
			complete:()=>{
				// uni.hideLoading();//
				toggleLoading(load, false)
			}
		})
	})
}

function toggleLoading(load, val){
	if(load && load.no){
		return ;
	}
	if (load && load.obj) {//tableloading
	    load.loading = load.loading ? load.loading : 'loading'
	    load.obj[load.loading] = val
	}else{//main
		if(val){
			uni.showLoading({
				title: load.text,
				mask: true,
			});
		}else{
			uni.hideLoading();//
		}
		
	}
}


