import Vue from 'vue'
//npm
import Router from 'uni-simple-router'

//vuex
import store from '@/add/vuex/store' 

//api
import  api   from '@/add/http/index.js'

Vue.use(Router)
//
const router = new Router({
    routes:ROUTES //
});

let whiteListName = ["pages/state/login", "pages/state/404", "pages/state/401"] //

/* 
params: {}
path: "/pages/home/list"
query: {} 
 */
//
router.beforeEach((to, from, next) => {
  // console.log(uni.getStorageSync("token"),"uni.getStorageSy")
  if (uni.getStorageSync("token")) { //token

  	// if(true){
  	if (to.path === '/pages/login') { // 
  		next({path: 'pages/home/index'})
  	} else { //token  
  		if(!store.state.user.userInfo.username){//
				//token 
				api.demo.get_user({token:uni.getStorageSync("token")},{}).then((res)=>{
					store.commit("user/userInfo_fn", res.data) // vuex
					next(`/pages/home/index`)
				})

  		} else {
  			next()//
  		}
  	}
  } else { //token
  	if (whiteListName.indexOf(to.name) !== -1) { //
  		next()//
  	} else { //
  		next(`/pages/state/login`)//afterEach,beforeEach, next()
  	}
  }
  
})
// 
// router.afterEach((to, from) => {
// })
export default router;